.slider {
  position: relative;
  user-select: none;
  touch-action: none;
  cursor: pointer;

  &_size {
    &_small {
      height: var(--ui-element-size-small);
      padding: 12px;

      & .slider__dot {
        top: 4px;
        width: 24px;
        height: 24px;
      }
    }

    &_medium {
      height: var(--ui-element-size-medium);
      padding: 15px;

      & .slider__dot {
        top: 5px;
        width: 30px;
        height: 30px;
      }
    }

    &_large {
      height: var(--ui-element-size-large);
      padding: 18px;

      & .slider__dot {
        top: 6px;
        width: 36px;
        height: 36px;
      }
    }
  }

  &:hover:not(&_dragging):not(&_disabled) {
    .slider__dot {
      border-color: var(--ui-color-primary-lightened);
    }

    .slider__rail__track {
      background-color: var(--ui-color-primary-lightened);
    }
  }

  &_dragging,
  &:focus-within {
    .slider__dot {
      border-color: var(--ui-color-primary-darkened);
    }

    .slider__rail__track {
      background-color: var(--ui-color-primary-darkened);
    }
  }

  &_disabled {
    cursor: not-allowed;

    .slider__rail__track {
      background-color: var(--ui-color-background-disabled);
    }

    .slider__dot {
      cursor: not-allowed;
      border-color: var(--ui-color-background-disabled);
    }
  }

  &__rail {
    position: relative;
    height: 100%;
    background-color: var(--ui-color-background-secondary);
    border-radius: var(--ui-element-border-radius-small);

    &__track {
      position: absolute;
      height: 100%;
      background-color: var(--ui-color-primary);
      border-radius: var(--ui-element-border-radius-small);
      left: 0;
      top: 0;
    }
  }

  &__dot {
    outline: none;
    padding: 0;
    margin: 0;
    -webkit-tap-highlight-color: transparent;
    pointer-events: none;

    position: absolute;
    left: 0;
    background-color: var(--ui-color-background-primary);
    border: var(--ui-border-width-large) solid var(--ui-color-primary);
    border-radius: 50%;

    &:focus-visible {
      outline: var(--ui-border-width-medium) solid var(--ui-color-primary);
      outline-offset: 4px;
    }
  }
}