.title {
  color: var(--ui-color-text-primary);
  font-weight: var(--ui-font-weight-bold);
  margin: 0;

  &_ellipsis {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

h1.title {
  font-size: var(--ui-font-size-heading-1);
  line-height: var(--ui-line-height-heading-1);
}

h2.title {
  font-size: var(--ui-font-size-heading-2);
  line-height: var(--ui-line-height-heading-2);
}

h3.title {
  font-size: var(--ui-font-size-heading-3);
  line-height: var(--ui-line-height-heading-3);
}

h4.title {
  font-size: var(--ui-font-size-heading-4);
  line-height: var(--ui-line-height-heading-4);
}

h5.title {
  font-size: var(--ui-font-size-heading-5);
  line-height: var(--ui-line-height-heading-5);
}